:root{
    --blueNav: rgb(16, 27, 73);
    --theBigGrey: rgb(3, 8, 33);
}

body{
    margin: 0;
    padding: 0;
}

.toCenter{
    text-align: center;
}


.App{
    color: rgb(255, 255, 255);
}

.app-content{
    display: flex;
    flex-direction: column;
}

.notbold{
    font-weight: normal;
}

.portfolio-img{
    width: 300px;
    height: 200px;
    border: var(--borderimg);

}

.totalFlexCol{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.center{
    text-align: center;
}

.GlobalSize{
    padding: 20px;
}

.totalFlexRow{
    display: flex;
    flex-wrap: wrap;
}

.bluelink{
    color: rgb(40, 160, 235);
}

.red{
    color: red;
}

.content-center{
    justify-content: center;
}

.transparent{
    color: transparent;
}

.blueNavBack{
    background-color: var(--blueNav);
}

.otherNavBack{
    background-color: black;
}

.theBigGreyBack{
    background-color: var(--theBigGrey);
}

.otherBigGreyBack{
    background-color: white;
    color: rgb(0, 0, 0);
}